import React, { useState, useMemo } from 'react';
import { useBoussoleDataService } from '../../Services/BoussoleDataService/BoussoleDataServiceProvider';
import { getNewTask } from '../../Helpers/tasksHelper';
import { Task } from '../../DataModel/Task';
import { Dialog } from '../Utility/Dialog/Dialog';
import { TaskForm } from './TaskForm';

export type TaskEditionDialogProps = {
  task: Task | undefined;
  onClose: () => void;
};

/** Enables editing a task. Responsible for creating or updating the task. Calls onClose() when done. */
export const TaskEditionDialog: React.FC<TaskEditionDialogProps> = ({ task, onClose }) => {
  const id = useMemo(() => (task ? task.id : undefined), [task]);
  const [editedTask, setEditedTask] = useState<Task>(task || getNewTask());
  const { createTask, updateTask, deleteTask } = useBoussoleDataService();

  const onSave = () => {
    if (id) {
      updateTask(id, editedTask);
    } else {
      createTask(editedTask);
    }
    onClose();
  };

  return (
    <Dialog
      title={id ? 'Modifier une tche' : 'Nouvelle tche'}
      buttons={[
        { text: 'Valider', onclick: onSave },
        { text: 'Annuler', onclick: onClose },
      ]}
    >
      <TaskForm
        task={editedTask}
        onChange={setEditedTask}
        onDelete={() => {
          if (id) deleteTask(id);
          onClose();
        }}
      />
    </Dialog>
  );
};
