import React from 'react';
import { TaskStateChoice } from './TaskState/TaskStateChoice';
import { Field } from '../Utility/Field/Field';
import { newState } from '../../Helpers/tasksHelper';
import { Task } from '../../DataModel/Task';
import { TagComponent } from '../Tags/TagComponent';
import { TagEditionDialog } from '../Tags/TagEditionDialog';
import { Button } from '../Utility/Button/Button';
import { Dialog } from '../Utility/Dialog/Dialog';

export type TaskFormProps = {
  task: Task;
  onChange: (task: Task) => void;
  onDelete: (id: string) => void;
};

/** Enables editing a task's data. Calls onChange when any change occurs. Does not have buttons. */
export const TaskForm: React.FC<TaskFormProps> = ({ task, onChange, onDelete }) => {
  const [tagEditFormOpen, setTagEditFormOpen] = React.useState(false);
  const [isConfirmDeleteDialogOpen, setIsConfirmDeleteDialogOpen] = React.useState(false);
  return (
    <>
      <TaskStateChoice state={task.state} onChange={(state) => onChange({ ...task, ...newState(state) })} />
      <Field label="titre">
        <input type="text" value={task.title} onChange={(event) => onChange({ ...task, title: event.target.value })} />
      </Field>
      <Field label="catgorie">
        <TagComponent
          tagId={task.tagId}
          active
          onClick={() => {
            setTagEditFormOpen(true);
          }}
        />
      </Field>
      <Field label="description" canMaximize>
        <textarea
          value={task.description}
          onChange={(event) => onChange({ ...task, description: event.target.value })}
        />
      </Field>
      {task.id && (
        <div>
          <Button onClick={() => setIsConfirmDeleteDialogOpen(true)}>Supprimer</Button>
        </div>
      )}
      {isConfirmDeleteDialogOpen && (
        <Dialog
          buttons={[
            {
              text: 'Supprimer',
              onclick: () => onDelete(task.id),
            },
            {
              text: 'Annuler',
              onclick: () => setIsConfirmDeleteDialogOpen(false),
            },
          ]}
        >
          Voulez-vous supprimer dfinitivement la tche "{task.title}" ?
        </Dialog>
      )}
      {tagEditFormOpen && (
        <TagEditionDialog
          tagId={task.tagId}
          onChange={(tagId) => {
            onChange({ ...task, tagId });
            setTagEditFormOpen(false);
          }}
          onClose={() => setTagEditFormOpen(false)}
        />
      )}
    </>
  );
};
